@import "vars.scss";
@import "modificators.scss";
@import "mixins.scss";

.pokeDetail {
    transition: all 0, 3s ease-in-out;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 10;
    background-color: rgba($orange, 0.5);
    @extend .m_df-cs;

    &__wrapper {
        width: 100%;
        height: auto;
        border-radius: 5px;
        padding: 5px;
    }

    &__detail {
        display: grid;
        grid-template-columns: 200px 1fr;
        grid-row: 1fr;

    }

    &__label {
        // top: 5px;
        display: block;
        position: relative;
        color: white;
        text-transform: capitalize;
    }

    &__value {
        display: block;
        position: relative;
    }

    &__action {
        display: block;
        position: relative;

        img {
            cursor: pointer;
        }
    }
}